@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100%;height:1920px;background:linear-gradient(180deg, #BF4704 20%, rgba(238,117,49,0) 100%);background-repeat:no-repeat}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;display:flex;flex-direction:column}

.header{height:80px;width:100%;left:0px;top:0px;background:linear-gradient(180deg, #66d8a8 8%, rgba(255,255,255,0.178) 50%, rgba(0,255,240,0) 70%);color:#ffffff;font-family:'Rubik', sans-serif;border-radius:0px 0px 50px 50px;display:flex;justify-content:space-around;flex-direction:row;align-items:center}.header-home_link,.header-projects_link,.header-contacts_link{text-decoration:none;color:#ffffff}.header-home_link:hover,.header-projects_link:hover,.header-contacts_link:hover{transition:0.3s;color:aqua}.header-home{margin-right:30%}

.title,.main__title{font-family:'Montserrat', sans-serif;font-size:36px;color:#fff;height:150px}.main__head{display:flex;flex-direction:column;opacity:0;height:80vh}.main__high-area{display:flex;flex-direction:row;justify-content:space-between}.transition{opacity:1;transition:1s}.logo{width:230px;height:200px;margin-left:30px}.main__img{position:relative;width:180px;height:200px;mix-blend-mode:normal;border-radius:50%;cursor:pointer;transition:all 0.5s ease-in-out;z-index:99}.hover,.main__logo:hover{transform:scaleX(1.1);transition:all 0.5s ease-in-out}.main__logo{transition:all 0.5s ease-in-out;position:absolute;width:230px;height:200px;border-radius:50%;background:conic-gradient(from 180deg at 50% 50%, #27DDB1 0deg, rgba(39,221,177,0) 360deg);animation:disclosure 2s 0s 1}@keyframes disclosure{0%{transform:scaleX(0.8);transition:all 0.5s ease-in-out}50%{transform:scaleX(1);transition:all 0.5s ease-in-out}75%{transform:scaleX(0.8);transition:all 0.5s ease-in-out}100%{transform:scaleX(1);transition:all 0.5s ease-in-out}}.telegram,.whatsapp{height:25px;width:25px;position:relative;left:-85px;top:85px;z-index:1;cursor:pointer}.telegram:hover,.whatsapp:hover{transform:scale(1.1, 1.1);transition:all 0.5s ease-in-out}.whatsapp{left:85px}.main__description{font-family:'Montserrat', sans-serif;font-size:18px;color:#ffffff;max-width:60vw;display:flex;justify-content:center;align-items:center;margin-right:30px}.skills{display:flex;flex-direction:column}.skills .skills-title{font-family:'Montserrat', sans-serif;font-size:36px;color:#fff;height:150px;display:flex;align-items:center;justify-content:center}.skills .skills-items{display:grid;grid-template-columns:1fr 1fr 1fr;align-items:center}.skills .skills-items .skills__item{font-size:24px;font-family:'Black Ops One', cursive;color:rgba(179,255,255,0.897)}.skills .skills-items img{width:70px;height:70px}.main__arrow{display:flex;justify-content:center}.arrow{height:60px;width:60px;border-style:solid;border-color:rgba(0,255,238,0.897);border-width:0px 3px 3px 0px;transform:rotate(45deg);transition:border-width 150ms ease-in-out;cursor:pointer;animation:bounce 2s 0s infinite}@keyframes bounce{0%{transform:translateY(0) rotate(45deg)}50%{transform:translateY(-15px) rotate(45deg)}100%{transform:translateY(0) rotate(45deg)}}.arrow:hover{border-bottom-width:6px;border-right-width:6px;border-color:white}@media screen and (max-width: 768px){.main__high-area{flex-direction:column;justify-content:space-between;align-items:center;height:55vh}.main__title{height:20vh}.logo{margin:0}.main__description{margin:0;font-size:1em}.main__head{height:100%}}

