.projects-title{height:15vh;width:100%;font-family:'Montserrat', sans-serif;font-size:36px;color:#ffffff;display:flex;justify-content:center}.projects{display:flex;justify-content:space-around;flex-wrap:wrap}

.project-block{height:300px;width:500px;background-color:rebeccapurple;overflow:hidden;display:flex;justify-content:center;align-items:center;background-color:#fff;margin:20px;transition:0.5s}.project-block:hover{transform:scale(1.1);transition:0.5s;background-color:#fff}.project-block__title{position:absolute;font-family:'Montserrat', sans-serif;font-size:24px;color:#ffffff;background-color:rgba(0,255,238,0.897);z-index:999}.project-block__title:hover{transform:scale(1.1);transition:0.5s;background-color:#000}.project-img{height:100%;width:100%;z-index:99;transition:0.5s}.project-img:hover{transform:scale(1.1);transition:0.5s;background-color:rgba(255,255,255,0.438)}.project-img-alternate{height:100px;width:100px;position:absolute}

